import {TextControl, Flex, FlexBlock, FlexItem, Button, Icon} from "@wordpress/components"
import "./index.scss"

(function () {
	let locked = false

	wp.data.subscribe(function() {
		const results = wp.data.select("core/block-editor").getBlocks().filter(function(block) {
			return block.name == "ourplugin/are-you-paying-attention" && block.attributes.correctAnswer == undefined
		})

		if(results.length && locked == false){
			locked = true
			wp.data.dispatch("core/editor").lockPostSaving("noanswer")
		}	
		if(!results.length && locked){
			locked = false
			wp.data.dispatch("core/editor").unlockPostSaving("noanswer")
		}

	})
})()

wp.blocks.registerBlockType("ourplugin/are-you-paying-attention" , {
  title: "Are You Paying Attention?",
  icon: "smiley",
  category: "common",
  attributes: {
    question: {type: "string"},
    answers: {type: "array" , default: [""]},
		correctAnswer: {type: "number" , default: undefined}
  }, 
  edit: EditComponent,
  save: function(props) {
    return null;
  }
});


function EditComponent(props) {

  function updateQuestion(value) {
    props.setAttributes({question: value})
  }

	function deleteAnswers(indexToDelete) {
		const newAnswers = props.attributes.answers.filter(function(x, index){
			return index != indexToDelete
		})
		props.setAttributes({answers: newAnswers})

		if(indexToDelete == props.attributes.correctAnswer){
			props.setAttributes({correctAnswer: undefined})
		}
	}

	function markAsCorrect(index) {
		props.setAttributes({correctAnswer: index})
	}

  return (
    <div className="paying-attention-edit-block">
      <TextControl  label="Question:" style={{fontSize:"20px"}} value={props.attributes.question} onChange={updateQuestion}/>
      <p style={{fontSize:"13px" , margin: "20px 0 8px 0"}}>Answers:</p>
        {props.attributes.answers.map(function(answer , index){
					return(
						<Flex>
						<FlexBlock>
							<TextControl value={answer} onChange={newValue => {
								const newAnswers = props.attributes.answers.concat([])
								newAnswers[index] = newValue
								props.setAttributes({answers: newAnswers})
							}} />
						</FlexBlock>
						<FlexItem>
							<Button onClick={() => markAsCorrect(index)}>
								<Icon icon={props.attributes.correctAnswer == index ? "star-filled" : "star-empty"} className="mark-as-correct" />
							</Button>
						</FlexItem>
						<FlexItem>
							<Button isLink className="attention-delete" onClick={() => deleteAnswers(index)}>Delete</Button>
						</FlexItem>
					</Flex>
					)
        })}
      <Button isPrimary onClick={() =>{
				props.setAttributes({answers: props.attributes.answers.concat([""])})
			}}>Add Another Answer</Button>
    </div>
  )
}